<template>
  <div>
    <v-card class="table filter">
      <v-card-title class="table_head">
        <div class="table_title mb-1">Filter:</div>
        <div class="table_title mb-2">
          <v-text-field
            v-model="name"
            label="Name"
            hide-details
            solo-inverted
            flat
            dense
            class="table_search mt-1"
          ></v-text-field>
          <v-menu
            v-model="menuStart"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                color="white"
                append-icon="mdi-calendar"
                v-model="dateStart"
                label="Start date"
                readonly
                dense
                flat
                solo-inverted
                clearable
                hide-details
                v-bind="attrs"
                v-on="on"
                class="datepicker mt-1"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="dateStart"
              @input="menuStart = false"
              header-color="blue darken-3"
            ></v-date-picker>
          </v-menu>
          <v-menu
            v-model="menuEnd"
            :close-on-content-click="false"
            transition="scale-transition"
            offset-y
            min-width="auto"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-text-field
                color="white"
                append-icon="mdi-calendar"
                v-model="dateEnd"
                label="End date"
                readonly
                dense
                flat
                solo-inverted
                clearable
                hide-details
                v-bind="attrs"
                v-on="on"
                class="datepicker mt-1"
              ></v-text-field>
            </template>
            <v-date-picker
              v-model="dateEnd"
              @input="menuEnd = false"
              header-color="blue darken-3"
            ></v-date-picker>
          </v-menu>
          <v-text-field
            v-model="email"
            label="Paypal E-mail"
            hide-details
            solo-inverted
            flat
            dense
            class="table_search mt-1"
          ></v-text-field>
        </div>
        <div class="table_title mb-1">
          <v-spacer></v-spacer>
          <div class="table_box table_box_buttons">
            <v-btn color="blue darken-3" class="mr-3 white--text">
              Search
            </v-btn>
            <v-btn
              color="blue darken-3"
              class="white--text"
              @click="resetInput()"
            >
              Reset
            </v-btn>
          </div>
          <v-spacer></v-spacer>
          <div class="table_box table_box_checkbox">
            <v-checkbox
              label="Apply current search filter"
              color="primary"
              hide-details
              dense
              class="mr-2 table_checkbox_current"
            ></v-checkbox>
            <v-btn @click="showDialogExcel()" x-small>
              Excel file Download
            </v-btn>
          </div>
        </div>
        <div class="table_title">
          <v-select
            class="table_select"
            v-model="sortBy"
            flat
            solo-inverted
            hide-details
            color="white"
            dense
            :items="keys"
            label="sort By"
          ></v-select>
          <v-spacer></v-spacer>
          <div class="table_box mt-1">
            <v-btn small color="mr-1"> Approval </v-btn>
            <v-btn color="error" small class="button_suspension">
              Cancel
            </v-btn>
          </div>
        </div>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="$store.state.withdrawalList"
        :search="name"
        class="table_data"
      >
        <template v-slot:top>
          <v-dialog v-model="dialogExcel" max-width="400px">
            <v-card>
              <v-container>
                <div
                  class="card_title text-center mb-8"
                  v-text="titleDialogExcel"
                ></div>
              </v-container>

              <v-card-actions class="justify-center">
                <v-btn color="primary" @click="closeDialogExcel()">
                  Confirm
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <v-dialog v-model="dialog" max-width="800px">
            <v-card>
              <v-card-title class="text-h5">
                <span>Details</span>
              </v-card-title>

              <v-container>
                <v-row class="justify-center">
                  <v-col cols="12" sm="8" md="8">
                    <div class="card_box mb-6">
                      <div class="card_subtitle">Name</div>
                      <div class="card_value" v-text="nameDialog"></div>
                      <div class="card_subtitle">Date</div>
                      <div class="card_value" v-text="dateRequest"></div>
                      <div class="card_subtitle">Holding Profit</div>
                      <div class="card_value" v-text="holdingProfit"></div>
                      <div class="card_subtitle">Withdrawal amount</div>
                      <div class="card_value" v-text="withdrawalAmount"></div>
                      <div class="card_subtitle">Means of request</div>
                      <div class="card_value" v-text="meansRequest"></div>
                      <div class="card_subtitle">Paypal E-mail</div>
                      <div class="card_value" v-text="paypalEmail"></div>
                      <div class="card_subtitle">Total withdrawal</div>
                      <div class="card_value" v-text="totalWithdrawal"></div>
                    </div>
                  </v-col>
                </v-row>
              </v-container>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="error" class="white--text"> Cancel </v-btn>
                <v-btn outlined> Approval </v-btn>
                <v-btn @click="closeDialog()" outlined> List </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>

        <template v-slot:item.checkbox="{ item }">
          <v-checkbox
            color="blue darken-3"
            v-model="item.checkbox"
            style="width: fit-content"
            hide-details
          ></v-checkbox>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon class="mr-2" @click="showDialog(item)">
            mdi-information-outline
          </v-icon>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      name: "",
      email: "",
      sortBy: "",
      keys: ["Newest", "Oldest"],
      headers: [
        { text: "Code", align: "start", value: "code" },
        { text: "Name", value: "name" },
        { text: "Date", value: "date" },
        { text: "Withdrawal amount", value: "amount" },
        { text: "Transaction fee", value: "fee" },
        { text: "Paypal E-mail", value: "email" },
        { text: "Select", value: "checkbox" },
        { text: "Details", value: "actions", sortable: false },
      ],
      dateStart: "",
      dateEnd: "",
      menuStart: false,
      menuEnd: false,
      dialog: false,
      titleDialogExcel: "Do you want to get the entire data into Excel?",
      dialogExcel: false,
      dateRequest: "",
      nameDialog: "",
      holdingProfit: "",
      withdrawalAmount: "",
      meansRequest: "",
      paypalEmail: "",
      totalWithdrawal: "",
    };
  },

  methods: {
    resetInput() {
      let inputCheckbox = document.querySelectorAll(
        ".table_head .v-input--selection-controls__input input"
      );

      inputCheckbox.forEach((element) => {
        if (element.checked) {
          element.click();
        }
      });

      this.name = "";
      this.email = "";
      this.sortBy = "";
      this.dateStart = "";
      this.dateEnd = "";
    },
    showDialog(item) {
      this.dialog = true;

      this.dateRequest = item.date;
      this.nameDialog = item.name;
      this.holdingProfit = item.holding;
      this.withdrawalAmount = item.amount;
      this.meansRequest = item.request;
      this.paypalEmail = item.email;
      this.totalWithdrawal = item.total;
    },
    closeDialog() {
      this.dialog = false;
    },
    showDialogExcel() {
      let isChecked = document.querySelector(".table_checkbox_current input");
      if (isChecked.checked) {
        this.titleDialogExcel =
          "Do you want to get the current search results in Excel?";
      } else {
        this.titleDialogExcel =
          "Do you want to get the entire data into Excel?";
      }
      this.dialogExcel = true;
    },
    closeDialogExcel() {
      this.dialogExcel = false;
    },
  },
};
</script>
